




import { Component, Vue } from "vue-property-decorator";

import { BIconCheckCircle, BIconXCircleFill } from "bootstrap-vue";
Vue.component("BIconXCircleFill", BIconXCircleFill);
Vue.component("BIconCheckCircle", BIconCheckCircle);

import dayjs from "dayjs";

import { getModule } from "vuex-module-decorators";

import users_module from "@/store/modules/users";
const users_store = getModule(users_module);
import problem_set_module from "@/store/modules/problem_sets";
const problem_set_store = getModule(problem_set_module);

import { ProblemSet, Dictionary } from "@/store/models";

import AddProblemSetModal from "./ProblemSetsManagerComponents/AddProblemSetModal.vue";
import EditProblemSetsModal from "./ProblemSetsManagerComponents/EditProblemSetsModal.vue";

import { hasReducedScoring } from "@/common";

@Component({
  name: "ProblemSetsManager",
  components: {
    AddProblemSetModal,
    EditProblemSetsModal,
  },
})
export default class ProblemSetsManager extends Vue {
  private num_rows = 10;
  private current_page = 1;
  private selected_sets: ProblemSet[] = [];
  private show_time = false;
  private filter_string = "";
  private problem_set_tracker = 1; // a hacky way to get reaction to adding/remove problem sets.

  private get fields() {
    const fields: {
      key: string;
      sortable: boolean;
      label?: string;
      formatter?: string;
    }[] = [
      { key: "set_id", sortable: true, label: "Name" },
      {
        key: "assigned_users",
        sortable: true,
        label: "Users",
        formatter: "numUsers",
      },
      {
        key: "problems",
        sortable: true,
        label: "# Probs.",
        formatter: "numProbs",
      },
      { key: "visible", sortable: true },
      //{ key: "enable_reduced_scoring", label: "RS" },
      {
        key: "open_date",
        sortable: true,
        label: "Open Date",
        formatter: "formatDate",
      },
      // { key: "reduced_scoring_date", sortable: true, label: "Red. Sc. Date" },
      {
        key: "due_date",
        sortable: true,
        label: "Due Date",
        formatter: "formatDate",
      },
      {
        key: "answer_date",
        sortable: true,
        label: "Answer Date",
        formatter: "formatDate",
      },
    ];
    if (hasReducedScoring()) {
      fields.splice(4, 0, {
        key: "enable_reduced_scoring",
        sortable: false,
        label: "RS",
      });
      fields.splice(6, 0, {
        key: "reduced_scoring_date",
        sortable: true,
        label: "Red. Sc. Date",
        formatter: "formatDate",
      });
    }
    return fields;
  }

  private get problem_sets() {
    return problem_set_store.problem_sets;
  }

  private get problem_sets_as_array() {
    return (
      this.problem_set_tracker &&
      Array.from(problem_set_store.problem_sets.values())
    );
  }

  private viewSetLink(set_id: string) {
    return "set-view/" + set_id;
  }

  private editRow(item: ProblemSet) {
    // tslint:disable-next-line
    this.selected_sets = [item];
    this.$bvModal.show("edit-problem-sets-modal");
  }

  private rowSelected(rows: ProblemSet[]) {
    this.selected_sets = rows;
  }

  private formatDate(_date: number) {
    return this.show_time
      ? dayjs.unix(_date).format("MM/DD/YY [at] hh:mm a")
      : dayjs.unix(_date).format("MM/DD/YYYY");
  }

  private numUsers(data: Dictionary<string | number>[]) {
    return data.length + "/" + users_store.users.length;
  }

  private numProbs(data: Dictionary<string | number>[]) {
    return data.length;
  }

  private async deleteSets() {
    const sets = this.selected_sets.map((_set) => _set.set_id);
    const conf = confirm(
      "Are you sure you want to delete the following sets? " + sets
    );

    // async can't be used in a forEach, so we use a map and wait for the map to finish

    if (conf) {
      this.selected_sets.map(async (_set) => {
        return await problem_set_store.removeProblemSet(_set);
      });
    }
    this.problem_set_tracker += 1;
  }
}
